import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Config_impressao } from '../../../modelo/Config_impressao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'config_impressao-dados-details',   templateUrl: 'config_impressao_dadosDetails.component.html' }) export class Config_impressaoDadosDetailsComponent implements OnInit {    config_impressao: Config_impressao;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.config_impressao.Config_impressao";                 let id = +params['id'];         this.config_impressao = new Config_impressao();         this.config_impressao.cd_config_impressao = id;         o.objetoJson = this.config_impressao;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a config_impressao. '; }      this.config_impressao = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


