import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'config_impressao-dados',   templateUrl: 'config_impressao_dados.component.html' }) export class Config_impressaoDadosComponent implements OnInit {       displayMode: Config_impressaoDadosDisplayModeEnum;     displayModeEnum = Config_impressaoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = Config_impressaoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = Config_impressaoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = Config_impressaoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum Config_impressaoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


